.row {
  display: flex;
  flex-direction: row;
}
.flex-1{
  flex:1
}
.column {
  display: flex;
  flex-direction: column;
}
.divider{
  width: 100%;
  height: 1px;
  margin-top: 2px;
  margin-bottom: 2px;
  background-color: #ababab;
}

.bold {
  font-weight: bold;
}
.justify-center {
  justify-content: center;
}

.justify-around {
  justify-content: space-around;
}

.justify-between {
  justify-content: space-between;
}

.align-center {
  align-items: center;
}

.align-start {
  align-items: flex-start;
}

.align-end {
  align-items: flex-end;
}

.m-0 {
  margin: 0;
}

.m-1 {
  margin: 5px;
}

.m-2 {
  margin: 10px;
}

.m-3 {
  margin: 15px;
}

.m-4 {
  margin: 20px;
}

.m-5 {
  margin: 25px;
}
.m-v-2 {
  margin-top: 10px;
  margin-bottom: 10px;
}
.p-0 {
  padding: 0;
}

.p-1 {
  padding: 5px;
}

.p-2 {
  padding: 10px;
}

.p-3 {
  padding: 15px;
}

.p-4 {
  padding: 20px;
}

.p-5 {
  padding: 25px;
}

.card-title{
 font-size: 27px;
}

.cursor-p{
  cursor:  pointer;
}


h2 {
  font-size: 18px;
}

[class*="col-"] {
  float: left;
  padding: 15px;
}


.header {
  color: #000;
  font-family: "Ruthie", sans-serif;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
  min-height: 10vh;
}


.home-text{
  font-size: 31px;
}

/* For mobile phones: */
.col-s-1 {
  width: 8.33%;
}

.col-s-2 {
  width: 16.66%;
}

.col-s-3 {
  width: 25%;
}

.col-s-4 {
  width: 33.33%;
}

.col-s-5 {
  width: 41.66%;
}

.col-s-6 {
  width: 50%;
}

.col-s-7 {
  width: 58.33%;
}

.col-s-8 {
  width: 66.66%;
}

.col-s-9 {
  width: 75%;
}

.col-s-10 {
  width: 83.33%;
}

.col-s-11 {
  width: 91.66%;
}

.col-s-12 {
  width: 100%;
}

.title-wrapper * {
  color: #ffffff;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  letter-spacing: .1em;
  line-height: 1em;
}
.d-none{
  display: none;
}
@media only screen and (min-width: 768px) {
  /* For tablets: */

  .home-text{
    font-size: 49px;
  }
  .row-md {
    display: flex;
    flex-direction: row;
  }
  .profile-text{
    font-size: 22px;
  }
  .column-md {
    display: flex;
    flex-direction: column;
  }

  .justify-md-center {
    justify-content: center;
  }

  .justify-md-around {
    justify-content: space-around;
  }

  .justify-md-between {
    justify-content: space-between;
  }

  .align-md-center {
    align-items: center;
  }

  .align-md-start {
    align-items: flex-start;
  }

  .align-md-end {
    align-items: flex-end;
  }

  .title-wrapper * {
    color: #ffffff;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    letter-spacing: .1em;
    line-height: 1em;
  }

  .floating-text {
    font: bold 31px 'Ubuntu', serif;
  }

  h2 {
    font-size: 18px;
  }

  .title-wrapper {
    position: absolute;
    top: 110%;
    left: 10%;
  }

  .col-md-1 {
    width: 8.33%;
  }

  .col-md-2 {
    width: 16.66%;
  }

  .col-md-3 {
    width: 25%;
  }

  .col-md-4 {
    width: 33.33%;
  }

  .col-md-5 {
    width: 41.66%;
  }

  .col-md-6 {
    width: 50%;
  }

  .col-md-7 {
    width: 58.33%;
  }

  .col-md-8 {
    width: 66.66%;
  }

  .col-md-9 {
    width: 75%;
  }

  .col-md-10 {
    width: 83.33%;
  }

  .col-md-11 {
    width: 91.66%;
  }

  .col-md-12 {
    width: 100%;
  }
}



.heading{
  font-size: 34px;
  font-weight: bold;
}

.body{
  min-height: 90vh;
}

.card{
  margin:20px;
  padding: 20px;
  min-height: 100px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 1px 1px 5px darkgray;
}

@media only screen and (min-width: 992px) {
  /* For desktop: */
  .home-text{
    font-size: 15vh;
  }
  .d-none-lg{
    display: none;
  }
  .row-lg {
    display: flex;
    flex-direction: row;
  }

  .column-lg {
    display: flex;
    flex-direction: column;
  }
  .profile-text{

  }
