    justify-content: center;
  }

  .justify-lg-around {
    justify-content: space-around;
  }
  .justify-lg-start {
    justify-content: flex-start;
  }

  .justify-lg-between {
    justify-content: space-between;
  }

  .align-lg-center {
    align-items: center;
  }

  .align-lg-start {
    align-items: flex-start;
  }

  .align-lg-end {
    align-items: flex-end;
  }

  .title-wrapper * {
    color: #ffffff;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
    letter-spacing: .1em;
    line-height: 1em;
  }

  .title-wrapper {
    position: absolute;
    top: 60%;
    left: 10%;
  }

  .floating-text {
